<template>
  <div>
    <div class="search_container" v-loading="submitLoading">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="财务组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" @change="changeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.receipt_sn" placeholder="请输入" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流公司" size="mini" v-if="!is_company_service">
          <el-select clearable v-model="params.logistics_id" filterable @focus="logisticsInvok" @change="getlogisticsChange">
            <el-option :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id" v-for="item in logisticsList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="往来类型" size="mini">
          <el-select clearable v-model="params.accounts_type_id" @focus="getCurrentAccountListInvok" @change="getCurrentAccountChange" filterable>
            <el-option :label="item.accounts_type_name" :value="item.accounts_type_id" :key="item.accounts_type_id" v-for="item in accountypeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          <el-select clearable v-model="params.employee_id" @focus="EmployeeListInvok" @change="employeeChange" placeholder="请选择" filterable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核状态" size="mini">
          <el-select clearable v-model="params.auditor_status">
            <el-option label="全部" :value="0">全部</el-option>
            <el-option label="未审核" :value="1">未审核</el-option>
            <el-option label="已审核" :value="2">已审核</el-option>
            <el-option label="作废" :value="3">作废</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="结算单号" size="mini">
          <el-input v-model="params.ec_order_sn" placeholder="请输入" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item label="备注" size="mini">
          <el-input v-model="params.remark" clearable placeholder="请输入备注" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item label="商城订单号" size="mini">
          <el-input v-model="params.shopping_order_sn" placeholder="请输入" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item label="创建人" size="mini">
          <el-input v-model="params.add_user_name" placeholder="请输入创建人" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addFun" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      <el-button class="iconfont icon-pass" type="primary" @click="examineFun" size="mini" v-if="authority.b_pass==1">审核</el-button>
      <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
      <el-button plain class="iconfont icon-zuofei" @click="abolishFun" size="mini" v-if="authority.b_invalid==1&&!is_company_service">作废</el-button>
      <el-button plain icon="el-icon-download" size="mini" @click="downLoadFile" v-if="!is_company_service">下载模板</el-button>
      <el-button plain icon="iconfont icon-daoru5" @click="dialogEmployee" v-if="authority.b_import==1&&!is_company_service" size="mini">导入</el-button>
    </div>
    <div class="table_container">
      <xTable v-if='tableColumn.length > 0' :ajax='{ url: "getReceiptList", params: params }' ref='xTable' :footerMethod='footerMethod' @getData='dealData' :tableColumn='tableColumn' />
    </div>
    <upload :options="options" @uploadFail='uploadFail' ref="upload">
      <div slot="content">说明:目前只支持预收款导入</div>
    </upload>
    <excelExport ref="excelExport" v-show="false" :headerTitle='headerTitle' titles="客户收款导入错误文件" :dataList='dataList'></excelExport>
  </div>
</template>

<script>
import excel from "common/components/plugin/upgradeExcel/upgradeExcel";
import { tableHeader } from "./config/receiptmanage_default";
import { opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "customeReceiptmanageDefault",
  mixins: [tableHeader],
  data() {
    return {
      authority: {},
      submitLoading: false, // 提交添加加载
      timeValue: [],
      headerTitle: [],
      dataList: [],
      loading: false,
      options: {
        // 导入配置
        title: "收款单导入",
        url: "/v2/custom/CustomerReceipt/importCustomerReceipt",
      },
      footerData: {},
      multipleSelection: [],
      tableHeight: 600,
    };
  },
  components: {
    excel,
  },
  methods: {
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    dealData(data) {
      if (data.data.code === 200) {
        this.footerData = data.data.data.desc;
      }
    },
    uploadFail(data) {
      if (data.code === 400) {
        this.$refs.upload.close();
        this.$confirm("是否下载错误文件?", "温馨提示！", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          this.headerTitle = data.data.title;
          this.dataList = data.data.data;
          this.$refs.excelExport.exportClick();
        });
      }
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].receipt_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .antiAuditorReceipt({
          receipt_id: rebateArr,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg || res.data.data,
              type: "success",
            });
          }
          this.$refs.xTable._getData();
          // getData(this.searchParams, 2); //获取销售订单列表
        });
      //弃审
    },
    // 接口 下载模板
    downLoadFile() {
      this.submitLoading = true; // 开启加载
      // 下载模板
      this.$axMethod
        .customDownloadFileUrl()
        .then(({ data }) => {
          this.submitLoading = false; // 关闭加载
          if (data.code == 200) {
            window.location.href = data.data.url;
          }
        })
        .catch((error) => {
          this.submitLoading = false; // 关闭加载
        });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "页小计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 5) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    changeDate(val) {
      if (val != null) {
        this.params.start_time = val[0];
        this.params.end_time = val[1];
      } else {
        this.params.start_time = "";
        this.params.end_time = "";
      }
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中项
      this.$set(this.params, "customer_id", ""); // 初始化部门下拉选中项
      this.$set(this.params, "logistics_id", ""); // 初始化物流公司下拉选中项
      this.$set(this.params, "employee_id", ""); // 初始化业务员下拉选中项
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          (obj) => obj.customer_id === val
        )[0].customer_name;
      }
    },
    getCurrentAccountChange(val) {
      if (val) {
        this.params.accounts_type_name = this.accountypeList.filter(
          (obj) => obj.accounts_type_id === val
        )[0].accounts_type_name;
      }
    },
    getlogisticsChange(val) {
      if (val) {
        this.params.logistics_name = this.logisticsList.filter(
          (obj) => obj.logistics_id === val
        )[0].logistics_name;
      }
    },
    employeeChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          (obj) => obj.employee_id === val
        )[0].employee_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
        return false;
      }
      let receiptID = this.multipleSelection.map((item) => {
        return item.receipt_id;
      });
      this.$axMethod
        .customAuditorPassCustomerReceipt({ receipt_id: receiptID.join() })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: data.msg || data.data,
              type: "success",
            });
            this.$refs.xTable._getData();
          }
        });
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning",
        });
        return false;
      }
      let receiptID = this.multipleSelection.map((item) => {
        return item.receipt_id;
      });
      this.$axMethod
        .customAuditorInvalid({ receipt_id: receiptID.join() })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: data.msg || data.data,
              type: "success",
            });
            this.$refs.xTable._getData();
          }
        });
    },
    onSearch(num) {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.$refs.xTable._getData(num);
    },
    onReset() {
      this.timeValue = [];
      this.$set(this, "params", {});
      this.onSearch(1);
    },
    addFun() {
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_add",
      });
    },
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息",
          type: "warning",
        });
        return false;
      }
      let receiptID = this.multipleSelection.map((item) => {
        return item.receipt_id;
      });
      this.$axMethod
        .customDelReceipt({ receipt_id: receiptID.join() })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: data.data,
              type: "success",
            });
            this.$refs.xTable._getData();
          }
        });
    },
    examine(scope, row) {
      //查看
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_search",
        query: {
          receipt_id: row.receipt_id,
        },
      });
    },
    handleEdit(scope, row) {
      //修改
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_add",
        query: { receipt_id: row.receipt_id },
      });
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    // 内部记账依据明细表传来数据并展示查看详情页面
    if (this.$route.query.receipt_id) {
      var receipt_id = this.$route.query.receipt_id;
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_search",
        query: {
          receipt_id: receipt_id,
        },
      });
    }
    this.onSearch(1);
  },
  activated() {
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id),
    });
    opthionList(this, localStorage);
    this.onSearch(window.goPage);
  },
};
</script>

<style scoped>
.table_sum {
  width: 100%;
  overflow: hidden;
  background: #f5f7fa;
}

.table_sum li {
  float: left;
  border: 1px solid #edf0f9;
  border-top: none;
  font-size: 12px;
  color: #1f2d3d;
  line-height: 23px;
  text-align: center;
  height: 23px;
  box-sizing: border-box;
  padding: 0px 10px;
}
</style>
