export const tableHeader = {
  data() {
    return {
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        },
        {
          title: '财务组织',
          field: 'company_name',
          width: 130
        },
        {
          title: '操作',
          width: 100,
          fixed: "right",
          slots: {
            default: (scope, h) => {
              var a = []
              if (this.authority.b_info == 1) {
                a.push(<el-button size="small" type="text" onClick={() => this.orderSee(scope.row)} > 查看 </el-button>)
              }
              a.push(<el-button size="small" type="text" disabled={scope.row.auditor_status !== 1 ? true : false}
                onClick={() => this.orderModify(scope.row)}  > 修改  </el-button>)
              return [
                a
              ]
            }
          }
        },
        {
          field: 'receipt_sn',
          width: 156,
          title: '单据号'
        },
        {
          field: 'customer_name',
          width: 150,
          title: '客户名称'
        },
        {
          field: 'accounts_type_name',
          width: 70,
          title: '收款类型'
        },
        {
          field: 'total_price',
          width: 100,
          title: '实际收款金额'
        },
        {
          title: '部门',
          field: 'department_name',
          width: 100
        }, {
          title: '业务员',
          field: 'buyer_name',
          width: 100
        },
        {
          title: '商城订单号',
          field: 'shopping_order_sn',
          width: 150
        },
        {
          title: '创建人',
          field: 'add_user_name',
          width: 84,
        },
        {
          title: '业务日期',
          field: 'business_time',
          width: 100,
        },
        {
          title: '单据时间',
          field: 'add_time',
          width: 150
        },
        {
          title: '审核状态',
          field: 'auditor_status_name',
          width: 70,
        },
        {
          title: '审核人',
          field: 'auditor_user_name',
          width: 100
        },
        {
          title: '审核时间',
          field: 'auditor_time',
          width: 150
        },
        {
          title: '修改人',
          field: 'update_user_name',
          width: 110
        },
        {
          title: '修改时间',
          field: 'update_time',
          width: 160
        },
        {
          title: '物流公司',
          field: 'business_time',
          width: 120,
          visible: Boolean(this.is_company_service)
        },
        {
          title: '结算单号',
          field: 'ec_order_sn',
          width: 120
        },
        {
          title: '备注',
          field: 'remark',
          width: 120
        }
      ]
    }
  },
  methods: {
    orderSee(row) {
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_search",
        query: {
          receipt_id: row.receipt_id
        }
      });
    },
    orderModify(row) {
      this.$router.push({
        path: "/custom/receiptmanage/receiptmanage_add",
        query: {
          receipt_id: row.receipt_id
        }
      });
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "本页小计";
          }
          if (column.property === "total_price") {
            return this.$utils.sum(data, column.property).toFixed(2)
          }
          return null;
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "总计";
          }
          if (column.property === 'total_price' && this.footerData.total_price) {
            return this.$publicFution.priceFormatNone((this.footerData.total_price - 0))
          }
          return null;
        })
      ]
    }
  }
}